<template>
  <div class="icons">
    <swiper :options="swiperOption">
      <swiper-slide v-for="(page,index) of pages" v-bind:key="index">
        <div 
          class="icon"
          v-for="item of page"
          v-bind:key="item.id"
        >
          <div class="icon-img">
            <img class="icon-img-content" v-bind:src="item.imgUrl" alt="">
          </div>
          <p class="icon-desc">{{item.desc}}</p>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
export default {
  name: 'HomeIcons',
  props: {
    list:Array
  },
  data() {
    return {
      // 自动播放
      swiperOption: {
        autoplay: false
      },
    }
  },
  computed: {
    pages () {
      const pages = []
      this.list.forEach((item,index) => {
        const page = Math.floor(index/8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(item)
      })
      return pages
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
  @import '~@/assets/styles/varibles.styl'
  @import '~@/assets/styles/mixins.styl';  
  .icons >>> .swiper-container
    height 0
    padding-bottom 50%

  .icons
    margin-top .2rem 
    .icon
      position relative
      float left 
      width 25%
      padding-bottom 25%
      .icon-img 
        position absolute
        top: 0
        left: 0
        right: 0
        bottom .44rem
        .icon-img-content
          display block
          margin 0 auto 
          height 100%
      .icon-desc
        position absolute
        left 0
        right 0
        bottom 0
        height .44rem
        line-height .44rem
        text-align center
        color $darkTextColor
        ellipsis()
</style>
