<template>
  <div>
    <div class="search"> 
      <input class="search-input" type="text" placeholder="请输入城市名或拼音" v-model="keyword">
    </div>
    <div 
      class="search-content"  
      ref="search"
      v-show="keyword"
    >
      <ul>
        <li 
          class="search-item border-bottom"
          v-for="item of list"
          v-bind:key="item.id"
          @click="handleCityClick(item.name)"  
        >{{item.name}}</li>
        <li class="search-item border-bottom" v-show="!list.length">没有找到匹配</li>
      </ul>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import { mapMutations } from 'vuex'
// export default导出一个对象
export default {
  name: 'CitySearch',
  props: {
    cities: Object
  },
  data() {
    return {
      keyword:"",
      list:[],
      timer: null
    }
  },
  watch: {
    keyword () {
      if(this.timer) {
        clearTimeout(this.timer)
      }
      if (!this.keyword) {
        this.list = []
        return
      }
      this.timer = setTimeout(() => {
        const result = []
        for (let i in this.cities) {
          this.cities[i].forEach((value) => {
            if(value.spell.indexOf(this.keyword) > -1 ||  value.name.indexOf(this.keyword) > -1){
              result.push(value)
            }
          })
        }
        this.list = result
      },100)
    }
  },
  mounted() {
    this.scroll = new BScroll(this.$refs.search)
  },
  methods: {
    handleCityClick(city) {
      // this.$store.commit("changeCity",city)
      this.changeCity(city)
      this.$router.push("/")
    },
    ...mapMutations(["changeCity"])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
  @import '~@/assets/styles/varibles.styl'
    
  .search
    height .72rem
    padding 0 .1rem
    background $bgColor
    .search-input 
      box-sizing border-box
      padding 0 .1rem
      width 100%
      height .62rem
      line-height .62rem
      text-align center
      border-radius .06rem
      color #666
  .search-content 
    overflow hidden
    position absolute
    z-index 1
    top 1.58rem
    left 0
    right 0
    bottom 0
    background #eee
    .search-item 
      line-height .62rem
      padding-left .2rem
      color #666
      background #fff

</style>
